<template>
  <div>
    <svg
      version="1.1"
      xmlns="http://www.w3.org/2000/svg"
      xmlns:xlink="http://www.w3.org/1999/xlink"
      x="0px"
      y="0px"
      style="display: none;"
    >
      <symbol id="wave">
        <path
          d="M420,20c21.5-0.4,38.8-2.5,51.1-4.5c13.4-2.2,26.5-5.2,27.3-5.4C514,6.5,518,4.7,528.5,2.7c7.1-1.3,17.9-2.8,31.5-2.7c0,0,0,0,0,0v20H420z"
        />
        <path
          d="M420,20c-21.5-0.4-38.8-2.5-51.1-4.5c-13.4-2.2-26.5-5.2-27.3-5.4C326,6.5,322,4.7,311.5,2.7C304.3,1.4,293.6-0.1,280,0c0,0,0,0,0,0v20H420z"
        />
        <path
          d="M140,20c21.5-0.4,38.8-2.5,51.1-4.5c13.4-2.2,26.5-5.2,27.3-5.4C234,6.5,238,4.7,248.5,2.7c7.1-1.3,17.9-2.8,31.5-2.7c0,0,0,0,0,0v20H140z"
        />
        <path
          d="M140,20c-21.5-0.4-38.8-2.5-51.1-4.5c-13.4-2.2-26.5-5.2-27.3-5.4C46,6.5,42,4.7,31.5,2.7C24.3,1.4,13.6-0.1,0,0c0,0,0,0,0,0l0,20H140z"
        />
      </symbol>
    </svg>

    <div class="water-jar">
      <div id="water" class="water" :style="getStyles(more)">
        <svg
          viewBox="0 0 560 20"
          class="water_wave water_wave_front"
          :style="`fill: ${more.color};`"
        >
          <use xlink:href="#wave" />
        </svg>
      </div>
    </div>

    <div class="water-jar">
      <div id="water" class="water" :style="getStyles(less)">
        <svg
          viewBox="0 0 560 20"
          class="water_wave water_wave_back"
          :style="`fill: ${less.color}; margin-bottom: -1px;`"
        >
          <use xlink:href="#wave" />
        </svg>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  props: ["percent1", "percent2"],
  data() {
    if (Number(this.percent1) > Number(this.percent2)) {
      return {
        more: {
          percent: this.percent1,
          color: "#ff5000"
        },
        less: {
          percent: this.percent2,
          color: "rgb(102, 102, 102)"
        }
      };
    } else {
      return {
        less: {
          percent: this.percent1,
          color: "rgb(102, 102, 102)"
        },
        more: {
          percent: this.percent2,
          color: "#00c389"
        }
      };
    }
  },
  methods: {
    getStyles(element) {
      const top = 100 - element.percent < 0 ? 0 : 100 - element.percent;
      return `transform: translate(0, ${top}%) !important; background: ${element.color}`;
    }
  }
};
</script>
