<template>
  <z-view v-if="data.sum" :label="labelCenter()" label-pos="top">
    <div v-if="!data.onCompare">
      <h1>{{ regionName(data.region_name) }}</h1>
      <br />
      <p>{{ AVG_LEVEL_OF_WATER }}</p>
      <percentDifferent
        :percentDiff="
          data.sum.sum_PERCENT_QUse - data.sum.sum_PERCENT_QUse_prev
        "
        size="18px"
      />
      <br />
      <h1 style="font-size: 60px;">{{ data.sum.sum_PERCENT_QUse }}%</h1>
      <waterAnimation :percent="data.sum.sum_PERCENT_QUse" />
    </div>
    <div v-if="data.onCompare">
      <h1>{{ regionName(data.region_name) }}</h1>
      <percentCompare
        :percentDiff="data.sum.sum_PERCENT_QUse_2 - data.sum.sum_PERCENT_QUse_1"
        isCenter="true"
      />
      <doubleWaterAnimation
        :percent1="data.sum.sum_PERCENT_QUse_1"
        :percent2="data.sum.sum_PERCENT_QUse_2"
      />
    </div>
    <div slot="extension">
      <div>
        <z-spot
          v-for="dam in data.dams"
          :angle="dam.angle"
          :distance="dam.distance"
          size="L"
          :key="dam.DAM_ID"
          :label="dam.DAM_NAME"
          :to-view="{ name: 'dam', params: { data: dam } }"
        >
          <div v-if="data.onCompare">
            <percentCompare
              :id="`${dam.DAM_ID}-${dam.id}`"
              :percentDiff="dam.PERCENT_DMD_QUse_2 - dam.PERCENT_DMD_QUse_1"
            />
            <doubleWaterAnimation
              :percent1="dam.PERCENT_DMD_QUse_1"
              :percent2="dam.PERCENT_DMD_QUse_2"
            />
          </div>
          <div v-if="!data.onCompare">
            <h5 :id="`${dam.DAM_ID}-${dam.id}`">{{ dam.PERCENT_DMD_QUse }}%</h5>
            <waterAnimation :percent="dam.PERCENT_DMD_QUse" />
            <percentDifferent
              :percentDiff="dam.PERCENT_DMD_QUse - dam.PERCENT_DMD_QUse_prev"
            />
          </div>
        </z-spot>
      </div>
    </div>
  </z-view>
</template>

<script>
import waterAnimation from "@/components/water/waterAnimation";
import doubleWaterAnimation from "@/components/water/doubleWaterAnimation";
import percentDifferent from "@/components/percentDifferent";
import percentCompare from "@/components/percentCompare";
import { regionName } from "@/utils/matchRegionName";
import { DAMS_POSITION } from "@/constants/position";
import { AVG_LEVEL_OF_WATER } from "@/language/thai";
import { compareLabel } from "@/utils/compareLabel";
export default {
  data() {
    return {
      data: {}
    };
  },
  components: {
    waterAnimation,
    doubleWaterAnimation,
    percentDifferent,
    percentCompare
  },
  created() {
    this.AVG_LEVEL_OF_WATER = AVG_LEVEL_OF_WATER;
  },
  computed: {
    // eslint-disable-next-line vue/return-in-computed-property
    retrieveParams() {
      if (this.$zircle.getParams() !== undefined) {
        return this.$zircle.getParams().data;
      }
    }
  },
  methods: {
    regionName,
    combineData(dams) {
      return dams.map((dam, index) => ({
        ...dam,
        ...DAMS_POSITION[this.data.region][index],
        onCompare: this.data.onCompare
      }));
    },
    labelCenter() {
      if (this.data.onCompare) {
        return compareLabel(this.data.sum.date_1, this.data.sum.date_2);
      } else {
        return "";
      }
    }
  },
  mounted() {
    this.data = this.retrieveParams;
    this.data.dams = this.combineData(this.data.dams);
  }
};
</script>
