<template>
  <h3 :style="`font-size: ${size || '12px'};`">
    <span
      class="text-background"
      :style="`background-color: ${color};`"
      v-tooltip.bottom="`${tooltip}`"
    >
      <i v-if="!!percent" :class="`fas fa-${icon}`" style="line-height: 2"></i>
      {{ this.percent }}%
    </span>
  </h3>
</template>
<script>
export default {
  props: ["percentDiff", "size"],
  data() {
    return {
      percent: this.roundNumber(Math.abs(this.percentDiff)),
      color: "",
      icon: "",
      tooltip: ""
    };
  },
  methods: {
    renderDiff() {
      if (this.percentDiff > 0) {
        this.color = "#00c389";
        this.icon = "caret-up";
        this.tooltip = this.tooltipContent("เพิ่มขึ้น");
      } else if (this.percentDiff < 0) {
        this.color = "#ff5000";
        this.icon = "caret-down";
        this.tooltip = this.tooltipContent("ลดลง");
      } else {
        this.color = "grey";
        this.tooltip = this.tooltipContent("เท่าเดิม");
      }
    },
    roundNumber(num) {
      return Math.round((num + Number.EPSILON) * 100) / 100;
    },
    tooltipContent(text) {
      return `${text} ${this.percentDiff !== 0 &&
        this.percent + "%"} เทียบจากปีที่แล้ว`;
    }
  },
  mounted() {
    this.renderDiff();
  }
};
</script>
<style>
.text-background {
  border-radius: 25px;
  padding: 1px 5px;
}
</style>
