<template>
  <z-view :label="labelCenter()" label-pos="top">
    <pulse-loader :loading="true"></pulse-loader>
    <div v-if="!onCompare">
      <h1>{{ renderDateTh(this.date) }}</h1>
      <br />
      <p>{{ AVG_TOTAL_WATER }}</p>
      <div v-if="isLoaded">
        <percentDifferent
          :percentDiff="sum_percent_qUse - sum_percent_qUse_prev"
          size="18px"
        />
        <br />
        <h1 style="font-size: 60px;">{{ sum_percent_qUse }}%</h1>
        <waterAnimation :percent="sum_percent_qUse" />
      </div>
    </div>
    <div v-if="onCompare">
      <div v-if="isLoaded">
        <h1>เฉลี่ยทั้งประเทศ</h1>
        <percentCompare
          :percentDiff="
            compareData.sum_PERCENT_QUse_2 - compareData.sum_PERCENT_QUse_1
          "
          isCenter="true"
        />
        <doubleWaterAnimation
          :percent1="compareData.sum_PERCENT_QUse_1"
          :percent2="compareData.sum_PERCENT_QUse_2"
        />
      </div>
    </div>
    <div slot="extension">
      <z-spot
        button
        :angle="-45"
        size="xs"
        :distance="100"
        @click.native="openCompareDialog = true"
        v-tooltip.right-end="
          `${onCompare ? 'เปลี่ยนวัน' : 'เปรียบเทียบ 2 วัน'}`
        "
      >
        <i
          class="fas fa-exchange-alt"
          style="font-size: 20px; line-height: 2;"
        ></i>
      </z-spot>
      <z-spot
        button
        :angle="-30"
        size="xs"
        :distance="100"
        @click.native="onCompare = false"
        v-if="onCompare"
        v-tooltip.right-end="`ออก`"
      >
        <i class="fas fa-times" style="font-size: 20px; line-height: 2;"></i>
      </z-spot>
      <z-spot
        button
        :angle="-30"
        size="xs"
        :distance="100"
        @click.native="openDateDialog = true"
        v-if="!onCompare"
        v-tooltip.right-end="`เลือกวันที่`"
      >
        <i
          class="fas fa-calendar-alt"
          style="font-size: 20px; line-height: 2;"
        ></i>
      </z-spot>
      <z-spot
        button
        :angle="-15"
        size="xs"
        :distance="100"
        @click.native="setToToday()"
        v-if="!onCompare"
        v-tooltip.right-end="`รีเซ็ต`"
      >
        <i class="fas fa-undo-alt" style="font-size: 20px; line-height: 2;"></i>
      </z-spot>
      <z-dialog v-if="openDateDialog">
        <h1>{{ SELECT_DATE_DIALOG }}</h1>
        <br />
        <input
          type="date"
          name="newDate"
          :value="date"
          :max="today"
          :min="MIN_DATE"
        />
        <br />
        <p style="font-size: 18px;">
          * {{ DATE_WARNING_DIALOG }} {{ this.dateWarning(MIN_DATE) }} -
          {{ this.dateWarning(date) }}
        </p>
        <br />
        <p v-if="isDateError" style="color: red; font-size: 20px;">
          {{ DATE_ERROR_DIALOG }}
        </p>
        <div slot="extension">
          <z-spot
            :angle="-30"
            size="s"
            :distance="110"
            @click.native="resetDateDialog()"
            style="background-color: red;"
          >
            <i class="fas fa-times"></i>
          </z-spot>
          <z-spot
            :angle="-55"
            size="s"
            :distance="110"
            @click.native="changeDate()"
            style="background-color: green;"
          >
            <i class="fas fa-check"></i>
          </z-spot>
        </div>
      </z-dialog>
      <z-dialog v-if="openCompareDialog">
        <h1>{{ SELECT_DATE_DIALOG }}</h1>
        <br />
        <input
          type="date"
          name="d1"
          :max="today"
          :min="MIN_DATE"
          :value="compareData.date_1"
        />
        <i
          class="fas fa-exchange-alt"
          style="font-size: 20px; line-height: 2; transform: rotate(90deg);"
        ></i>
        <input
          type="date"
          name="d2"
          :max="today"
          :min="MIN_DATE"
          :value="compareData.date_2"
        />
        <br />
        <p style="font-size: 18px;">
          * {{ DATE_WARNING_DIALOG }} {{ this.dateWarning(MIN_DATE) }} -
          {{ this.dateWarning(date) }}
        </p>
        <br />
        <p v-if="isDateError" style="color: red; font-size: 20px;">
          {{ DATE_ERROR_DIALOG }}
        </p>
        <div slot="extension">
          <z-spot
            :angle="-30"
            size="s"
            :distance="110"
            @click.native="openCompareDialog = false"
            style="background-color: red;"
          >
            <i class="fas fa-times"></i>
          </z-spot>
          <z-spot
            :angle="-55"
            size="s"
            :distance="110"
            @click.native="setCompare()"
            style="background-color: green;"
          >
            <i class="fas fa-check"></i>
          </z-spot>
        </div>
      </z-dialog>
      <div slot="extension">
        <div v-if="shouldRenderRegions()">
          <z-spot
            v-for="region in onCompare ? compareData.regions : regions"
            size="L"
            :angle="region.angle"
            :distance="region.distance"
            :label="regionName(region.region_name)"
            :key="region.name"
            :to-view="{ name: 'region', params: { data: region } }"
          >
            <div v-if="!onCompare">
              <h5>{{ region.sum.sum_PERCENT_QUse }}%</h5>
              <percentDifferent
                :percentDiff="
                  region.sum.sum_PERCENT_QUse - region.sum.sum_PERCENT_QUse_prev
                "
              />
              <waterAnimation :percent="region.sum.sum_PERCENT_QUse" />
            </div>
            <div v-if="onCompare">
              <percentCompare
                :percentDiff="
                  region.sum.sum_PERCENT_QUse_2 - region.sum.sum_PERCENT_QUse_1
                "
              />
              <doubleWaterAnimation
                :percent1="region.sum.sum_PERCENT_QUse_1"
                :percent2="region.sum.sum_PERCENT_QUse_2"
              />
            </div>
          </z-spot>
        </div>
      </div>
    </div>
  </z-view>
</template>

<script>
import axios from "axios";
import {
  AVG_TOTAL_WATER,
  SELECT_DATE_DIALOG,
  DATE_ERROR_DIALOG,
  DATE_WARNING_DIALOG
} from "@/language/thai";
import waterAnimation from "@/components/water/waterAnimation";
import doubleWaterAnimation from "@/components/water/doubleWaterAnimation";
import percentDifferent from "@/components/percentDifferent";
import percentCompare from "@/components/percentCompare";
import { regionName } from "@/utils/matchRegionName";
import { compareLabel } from "@/utils/compareLabel";
import { REGIONS_POSITION } from "@/constants/position";
import { SHORT_MONTH_TH } from "@/constants/thaiMonth";
// import { mockData } from "@/newCompareWater";
export default {
  data() {
    return this.getDefaultData();
  },
  components: {
    waterAnimation,
    doubleWaterAnimation,
    percentDifferent,
    percentCompare
  },
  created() {
    this.AVG_TOTAL_WATER = AVG_TOTAL_WATER;
    this.SELECT_DATE_DIALOG = SELECT_DATE_DIALOG;
    this.DATE_ERROR_DIALOG = DATE_ERROR_DIALOG;
    this.DATE_WARNING_DIALOG = DATE_WARNING_DIALOG;
    this.MIN_DATE = "2017-01-01";
    this.REGIONS_POSITION = REGIONS_POSITION;
    this.URL = "http://104.196.33.85";
  },
  methods: {
    regionName,
    getWaterPercentByDate(date) {
      axios
        .get(`${this.URL}/water/getWater?date=${date}`)
        .then(res => {
          if (res.length === 0) {
            this.sum_percent_qUse = 0;
          } else {
            this.regions = this.combineRegionsData(res.data.regions);
            this.sum_percent_qUse = res.data.sum_all.sum_PERCENT_QUse;
            this.sum_percent_qUse_prev = res.data.sum_all.sum_PERCENT_QUse_prev;
            this.isLoaded = true;
          }
        })
        .catch(err => {
          console.log(err);
          this.sum_percent_qUse = 0;
        });
    },
    formatDate(d) {
      let month = "" + (d.getMonth() + 1),
        date = "" + d.getDate();
      const year = d.getFullYear();

      if (month.length < 2) month = "0" + month;
      if (date.length < 2) date = "0" + date;

      return [year, month, date].join("-");
    },
    getWaterComparePercent() {
      // this.isLoaded = true;
      // this.compareData = {
      //   ...this.compareData,
      //   regions: this.combineRegionsData(mockData.regions),
      //   sum_PERCENT_QUse_1: mockData.sum_all.sum_PERCENT_QUse_1,
      //   sum_PERCENT_QUse_2: mockData.sum_all.sum_PERCENT_QUse_2
      // };
      axios
        .get(
          `${this.URL}/water/compareWater?firstDate=${this.compareData.date_1}&secondDate=${this.compareData.date_2}`
        )
        .then(res => {
          if (res.length === 0) {
            this.compareData.sum_PERCENT_QUse_1 = 0;
            this.compareData.sum_PERCENT_QUse_2 = 0;
          } else {
            this.compareData = {
              ...this.compareData,
              regions: this.combineRegionsData(res.data.regions),
              sum_PERCENT_QUse_1: res.data.sum_all.sum_PERCENT_QUse_1,
              sum_PERCENT_QUse_2: res.data.sum_all.sum_PERCENT_QUse_2
            };
            this.isLoaded = true;
          }
        })
        .catch(err => {
          console.log(err);
          this.compareData.sum_PERCENT_QUse_1 = 0;
          this.compareData.sum_PERCENT_QUse_2 = 0;
        });
    },
    changeDate() {
      const newDate = document.querySelector("input[name=newDate]").value;
      if (this.isDateInvalid(newDate)) {
        this.isDateError = true;
      } else if (newDate !== this.date) {
        this.setToDate(newDate);
      } else {
        this.resetDateDialog();
      }
    },
    resetDateDialog() {
      this.openDateDialog = false;
      this.isDateError = false;
    },
    setToToday() {
      if (this.today !== this.date) {
        this.setToDate(this.today);
      }
    },
    setToDate(date) {
      Object.assign(this.$data, this.getDefaultData());
      this.date = date;
      this.getWaterPercentByDate(this.date);
    },
    getDefaultData() {
      return {
        isDateError: false,
        openDateDialog: false,
        openCompareDialog: false,
        isLoaded: false,
        onCompare: false,
        today: this.formatDate(new Date()),
        date: "",
        sum_percent_qUse: "",
        sum_percent_qUse_prev: "",
        regions: [],
        compareData: {
          date_1: this.formatDate(this.lastYearDate(new Date())),
          date_2: this.formatDate(new Date()),
          sum_PERCENT_QUse_1: "",
          sum_PERCENT_QUse_2: "",
          regions: []
        }
      };
    },
    renderDate(date) {
      const d = new Date(date);
      return `${d.toLocaleString("default", {
        month: "short"
      })} ${d.getDate()}, ${d.getFullYear()}`;
    },
    renderDateTh(date) {
      const d = new Date(date);
      return `${d.getDate()} ${SHORT_MONTH_TH[d.getMonth()]} ${d.getFullYear() +
        543 -
        2500}`;
    },
    shouldRenderRegions() {
      return this.isLoaded && !this.openDateDialog && !this.openCompareDialog;
    },
    combineRegionsData(regions) {
      return regions.map((region, index) => ({
        ...region,
        ...this.REGIONS_POSITION[index],
        onCompare: this.onCompare
      }));
    },
    setCompare() {
      const d1 = document.querySelector("input[name=d1]").value;
      const d2 = document.querySelector("input[name=d2]").value;
      this.isLoaded = false;
      if (d1 === d2 || this.isDateInvalid(d1) || this.isDateInvalid(d2)) {
        this.isDateError = true;
      } else {
        if (d1 > d2) {
          this.compareData.date_1 = d2;
          this.compareData.date_2 = d1;
        } else {
          this.compareData.date_1 = d1;
          this.compareData.date_2 = d2;
        }
        this.isDateError = false;
        this.openCompareDialog = false;
        this.onCompare = true;
        this.getWaterComparePercent();
      }
    },
    labelCenter() {
      if (this.onCompare) {
        return compareLabel(this.compareData.date_1, this.compareData.date_2);
      } else {
        return "";
      }
    },
    lastYearDate(date) {
      var d = new Date(date);
      d.setFullYear(d.getFullYear() - 1);
      return d;
    },
    isDateInvalid(date) {
      if (
        new Date(date) > new Date(this.today) ||
        new Date(date) < new Date(this.MIN_DATE)
      ) {
        return true;
      }
      return false;
    },
    dateWarning(date) {
      const d = date.split("-");
      return d[2] + "/" + d[1] + "/" + d[0];
    }
  },
  mounted() {
    this.date = this.today;
    this.getWaterPercentByDate(this.date);
  }
};
</script>
