import { NOR, SOU, NOR_EAST, EAST, CENT, WEST } from "../language/thai";

export const regionName = region => {
  switch (region) {
    case "เหนือ":
      return NOR;
    case "ใต้":
      return SOU;
    case "ตะวันออกเฉียงเหนือ":
      return NOR_EAST;
    case "ตะวันออก":
      return EAST;
    case "กลาง":
      return CENT;
    case "ตะวันตก":
      return WEST;
    default:
      return "";
  }
};
