<template>
  <h1 :style="`font-size: ${isCenter ? '60px' : '25px'}`">
    <i v-if="!!percent" :class="`fas fa-${icon}`" style="line-height: 2;"></i>
    {{ this.percent }}%
  </h1>
</template>
<script>
export default {
  props: ["percentDiff", "isCenter"],
  data() {
    return {
      percent: this.roundNumber(Math.abs(this.percentDiff)),
      icon: ""
    };
  },
  methods: {
    renderDiff() {
      if (this.percentDiff > 0) {
        this.icon = "caret-up";
      } else if (this.percentDiff < 0) {
        this.icon = "caret-down";
      } else {
        this.icon = "equals";
      }
    },
    roundNumber(num) {
      return Math.round((num + Number.EPSILON) * 100) / 100;
    }
  },
  mounted() {
    this.renderDiff();
  }
};
</script>
