<template>
  <div>
    <svg
      version="1.1"
      xmlns="http://www.w3.org/2000/svg"
      xmlns:xlink="http://www.w3.org/1999/xlink"
      x="0px"
      y="0px"
      style="display: none;"
    >
      <symbol id="wave">
        <path
          d="M420,20c21.5-0.4,38.8-2.5,51.1-4.5c13.4-2.2,26.5-5.2,27.3-5.4C514,6.5,518,4.7,528.5,2.7c7.1-1.3,17.9-2.8,31.5-2.7c0,0,0,0,0,0v20H420z"
        />

        <path
          d="M420,20c-21.5-0.4-38.8-2.5-51.1-4.5c-13.4-2.2-26.5-5.2-27.3-5.4C326,6.5,322,4.7,311.5,2.7C304.3,1.4,293.6-0.1,280,0c0,0,0,0,0,0v20H420z"
        />

        <path
          d="M140,20c21.5-0.4,38.8-2.5,51.1-4.5c13.4-2.2,26.5-5.2,27.3-5.4C234,6.5,238,4.7,248.5,2.7c7.1-1.3,17.9-2.8,31.5-2.7c0,0,0,0,0,0v20H140z"
        />

        <path
          d="M140,20c-21.5-0.4-38.8-2.5-51.1-4.5c-13.4-2.2-26.5-5.2-27.3-5.4C46,6.5,42,4.7,31.5,2.7C24.3,1.4,13.6-0.1,0,0c0,0,0,0,0,0l0,20H140z"
        />
      </symbol>
    </svg>

    <div class="water-jar">
      <div id="water" class="water" :style="getStyles()">
        <svg viewBox="0 0 560 20" class="water_wave water_wave_back" :style="getWaveBackStyles()">
          <use xlink:href="#wave" />
        </svg>

        <svg
          viewBox="0 0 560 20"
          class="water_wave water_wave_front"
          :style="getWaveFrontStyles()"
        >
          <use xlink:href="#wave" />
        </svg>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  props: ["percent"],
  methods: {
    getStyles() {
      const top = 100 - this.percent < 0 ? 0 : 100 - this.percent;
      return `transform: translate(0, ${top}%) !important; background: ${this.getWaveFrontColor()}`;
    },
    getWaveFrontColor() {
      let color;
      if (this.percent <= 30) {
        color = "#dba522";
      } else if (this.percent <= 50) {
        color = "#279445";
      } else if (this.percent <= 80) {
        color = "#3366CC";
      } else if (this.percent <= 100) {
        color = "#c21808";
      } else {
        color = "#960001";
      }
      return color;
    },
    getWaveBackStyles() {
      let color;
      if (this.percent <= 30) {
        color = "#fada5e";
      } else if (this.percent <= 50) {
        color = "#53ad6c";
      } else if (this.percent <= 80) {
        color = "#6593f0";
      } else if (this.percent <= 100) {
        color = "#eb5a46";
      } else {
        color = "#960001";
      }
      return `fill: ${color};`;
    },
    getWaveFrontStyles() {
      return `fill: ${this.getWaveFrontColor()};`;
    }
  }
};
</script>
<style>
@import "./waterAnimation.css";
</style>
