import { MONTH_TH } from "@/constants/thaiMonth";

export const compareLabel = (date1, date2) => {
  return `ระดับน้ำเมื่อวันที่ ${getFullDate(
    date2
  )}                     เทียบกับ ${getFullDate(date1)}`;
};

const getFullDate = date => {
  const d = new Date(date);
  return `${d.getDate()} ${MONTH_TH[d.getMonth()]} ${d.getFullYear() + 543}`;
};
