<template>
  <z-view v-if="data.DAM_ID" :label="labelCenter()" label-pos="top">
    <div v-if="!data.onCompare">
      <h1>{{ data.DAM_NAME }}</h1>
      <h6 style="margin-top: 5px;">จังหวัด{{ data.DAM_Province }}</h6>
      <br />
      <p>{{ LEVEL_OF_WATER }}</p>
      <percentDifferent
        v-if="percentDiff"
        :percentDiff="percentDiff"
        size="18px"
      />
      <br />
      <h1 style="font-size: 60px;">{{ data.PERCENT_DMD_QUse }}%</h1>
      <waterAnimation :percent="data.PERCENT_DMD_QUse" />
    </div>
    <div v-if="data.onCompare">
      <h1>{{ data.DAM_NAME }}</h1>
      <percentCompare
        :percentDiff="data.PERCENT_DMD_QUse_2 - data.PERCENT_DMD_QUse_1"
        isCenter="true"
      />
      <doubleWaterAnimation
        :percent1="data.PERCENT_DMD_QUse_1"
        :percent2="data.PERCENT_DMD_QUse_2"
      />
    </div>
    <div slot="extension">
      <a :href="getInfoLink()" target="_blank">
        <z-spot
          button
          size="xs"
          :angle="-40"
          :distance="100"
          v-tooltip.right-end="`ข้อมูลเพิ่มเติม`"
        >
          <i class="fas fa-info" style="font-size: 20px; line-height: 2;" />
        </z-spot>
      </a>
    </div>
  </z-view>
</template>

<script>
import waterAnimation from "@/components/water/waterAnimation";
import doubleWaterAnimation from "@/components/water/doubleWaterAnimation";
import percentDifferent from "@/components/percentDifferent";
import percentCompare from "@/components/percentCompare";
import { LEVEL_OF_WATER } from "@/language/thai";
import { compareLabel } from "@/utils/compareLabel";
export default {
  data() {
    return {
      data: {},
      percentDiff: ""
    };
  },
  components: {
    waterAnimation,
    doubleWaterAnimation,
    percentDifferent,
    percentCompare
  },
  created() {
    this.LEVEL_OF_WATER = LEVEL_OF_WATER;
  },
  computed: {
    // eslint-disable-next-line vue/return-in-computed-property
    retrieveParams() {
      if (this.$zircle.getParams() !== undefined) {
        return this.$zircle.getParams().data;
      }
    }
  },
  methods: {
    getInfoLink() {
      const url = "http://app.rid.go.th:88/reservoir/rsvmiddle/dam_detail";
      if (this.data.onCompare) {
        return `${url}/${this.data.DAM_ID}/${this.data.DAM_Date_1}/${this.data.DAM_Date_2}`;
      }
      return `${url}/${this.data.DAM_ID}/${this.data.DAM_Date}/${this.data.DAM_Date}`;
    },
    labelCenter() {
      if (this.data.onCompare) {
        return compareLabel(this.data.DAM_Date_1, this.data.DAM_Date_2);
      } else {
        return "";
      }
    }
  },
  mounted() {
    this.data = this.retrieveParams;
    if (!this.data.onCompare) {
      this.percentDiff =
        this.data.PERCENT_DMD_QUse - this.data.PERCENT_DMD_QUse_prev;
    }
  }
};
</script>
